@import "../assets/style.less";

.navBar {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
    width: 100%;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index:2;
    height: 8vh;
    background-color: #colors[secondary];
}

/* Each card used on the site */
.navItem {
    float: right;
    color: black;
    height: 100%;
    margin: 2.5vh 1vw 1vh 1vw;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    cursor: pointer;
}

.navItems {
    display: flex;
    justify-content: center;
    flex-direction: row;
    width: 100vw;
}
@primary-color: #3d5c82;@link-color: #3d5c82;@success-color: #52c41a;@warning-color: #faad14;@error-color: #f5222d;@font-size-base: 14px;@heading-color: rgba(0, 0, 0, 0.85);@text-color: rgba(0, 0, 0, 0.65);@text-color-secondary: rgba(0, 0, 0, 0.45);@disabled-color: rgba(0, 0, 0, 0.25);@border-radius-base: 4px;@border-color-base: #d9d9d9;@box-shadow-base: 0 2px 8px rgba(0, 0, 0, 0.15);